import React from "react";
import { Link as RouterLink } from "react-router-dom";
import {
  Avatar,
  Box,
  Card,
  CardContent,
  Grid,
  Button,
  LinearProgress,
  Typography,
  makeStyles,
  colors,
  useTheme,
} from "@material-ui/core";
import { FileText as FileTextIcon } from "react-feather";

interface CurrentRequestProgressProps {
  id: number;
  status: string;
  progress: number;
}

const CurrentRequestProgress = ({
  id,
  status,
  progress,
}: CurrentRequestProgressProps) => {
  const classes = useStyles();
  const theme = useTheme();

  const body = (
    <Card className={classes.root + " dashboard-stat-card"}>
      <CardContent>
        <Grid container justify="space-between" spacing={3}>
          <Grid item>
            <Typography color="textSecondary" gutterBottom variant="h6">
              RESUME CURRENT SUBMISSION
            </Typography>
            <Typography color="textPrimary" variant="h3">
              {(status === "DRAFT" && `${progress}%`) ||
                status.replaceAll("_", " ")}
            </Typography>
          </Grid>
          <Grid item>
            <Avatar
              variant="rounded"
              style={{ backgroundColor: theme.palette.secondary.main }}
            >
              <FileTextIcon />
            </Avatar>
          </Grid>
        </Grid>
        <Box
          style={{
            display: "flex",
            alignItems: "center",
            marginBottom: -11,
            marginTop: (status === "IN_REVIEW" && 15) || 0,
          }}
        >
          <LinearProgress
            value={progress}
            variant="determinate"
            style={{
              width: "100%",
              marginRight: (status !== "IN_REVIEW" && 15) || 0,
            }}
          />
          {status !== "IN_REVIEW" && <Button size="small">Resume</Button>}
        </Box>
      </CardContent>
    </Card>
  );

  return (
    (status !== "IN_REVIEW" && (
      <RouterLink to={`/client/request/${id}`}>{body}</RouterLink>
    )) ||
    body
  );
};

export default CurrentRequestProgress;

const useStyles = makeStyles(() => ({
  root: {
    height: "100%",
  },
  avatar: {
    backgroundColor: colors.orange[600],
    height: 56,
    width: 56,
  },
}));
